body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.download-container {
  width: 100%;
}

.download-button {
  color: #000;
  background-color: #AADFED;
  font-weight: 900;
  font-size: 16px;
  text-transform: uppercase;

  margin-top: 1rem;
  padding: 12px;
  
  width: 100%;
  
  border: 2px solid #000;
  box-shadow: 4px 4px 0 #000;
  outline: none;
}

.download-button:active {
  -webkit-transition: all 0.3ms;
  transition: all 0.3ms;
  box-shadow: 2px 2px 0 #000;
  -webkit-transform: scale(.995, .995);
          transform: scale(.995, .995);
}

.download-wait-text {
  color: #000;
  font-weight: 900;
  font-size: 16px;
  text-transform: uppercase;

  margin-top: 1rem;
  padding: 12px;
}
.input-container {
  display: flex;
  width: 100%;
}

.input-container__input {
  outline: none;
  box-sizing: border-box;

  color: #000;
  background-color: #FFF;
  font-weight: 900;
  font-size: 16px;

  margin-top: 1rem;
  padding: 12px;

  width: 100%;

  border: 2px solid #000;
}

.input-container__button {
  color: #000;
  background-color: #FFF;
  font-weight: 900;
  font-size: 16px;

  margin-top: 1rem;
  padding: 12px;
  margin-left: 1rem;

  border: 2px solid #000;
}
.title-container {

}

.title-text {
  color: black;
  font-size: 28px;
  font-weight: 900;
}
body {
  background-color: whitesmoke;
  height: 100%;
  max-height: 100vh;
  margin: 0;
}

.App {
  height: 100vh;
  display:         flex;
  align-items: center;
  justify-content: center;
}

.App-container {
  flex-flow: column;

  padding: 1rem;

  border: 2px solid #000;
  width: auto;
  max-width: 480px;
  background-color: white;
}

.App-container #p {
  color: #000;
  background-color: #FFF;
  font-weight: 900;
  font-size: 16px;
}

@media screen and (max-width: 400px) {
  .App {
    height: 100%;
  }

  .App-container {
    height: 100%;
  }
}
